export default {
	data() {
		return {
			login:false,
			phoneInfo:null,
			userInfo:null
			
		}
	},
	methods: {
		toLogin()
		{
			uni.navigateTo({
				url: "../login/login"
			})
		},
		toSetting()
		{
			uni.navigateTo({
				url: "../setting/setting"
			})
		}
	},
	onLoad() {
		var me = this;
		
		uni.vibrateLong({
			success() {
				
			}
		})
		uni.getSystemInfo({
			success:function(res){
				me.phoneInfo = res.model;
			}
		})
	},
	onShow() {
		var me = this;
		uni.request({
			url: me.serverUrl + '/login/status',
			method: 'GET',
			data: {},
			success: res => {
				console.log(res.data)
				if(res.data.status==200)
				{
					// 仍旧是登录状态
					me.login = true;
					me.userInfo = uni.getStorageSync('userInfo')
				}else
				{
					me.login = false;
					uni.removeStorageSync('userInfo')
				}
				
			},
			fail: () => {},
			complete: () => {}
		});
	}
}
